body {
  font-family: Open Sans, sans-serif;
}

.temp-span {
  font-weight: bold;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #000;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-top: 20px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  display: none;
}

.cat-image {
  width: 550px;
  height: 400px;
}

.cat-info {
  flex-direction: row;
  padding: 20px;
}

.description {
  max-width: 350px;
}

.is-hidden {
  display: none;
}

